
import { defineComponent } from 'vue'
import Tree from "@/shared/components/tree/tree/tree.vue";
import TreeDataService from '@/shared/services/TreeDataService';
import { NodeData } from '@/shared/components/tree/interface';
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const';
import { COM20500111_REQ_BODY, COM20500111_RES, MenuInfo } from '@/shared/type/COM20500111.type';
import { COM20500112_REQ_BODY, COM20500112_RES } from "@/shared/type/COM20500112.type";
import { COM20500121_REQ_BODY, COM20500121_RES } from '@/shared/type/COM20500121.type';
import { COM20500141_REQ_BODY, COM20500141_RES } from '@/shared/type/COM20500141.type';
import { COM20500131_REQ_BODY, COM20500131_RES } from '@/shared/type/COM20500131.type';
import { modalController } from '@ionic/vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue';
// import SelectOptionTemp from "@/shared/components/SelectOptionTemp/SelectOptionTemp.vue"
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { screenMode } from '@/shared/type/Approval.type';
const treeService = new TreeDataService();
export default defineComponent({
    name: "com205001sc00Vue",
    components: {
        Tree,
        BreadCrumb,
        SelectOption,
        ModifyCard,
        // SelectOptionTemp
    },
    data() {
        const modifyCard = {
            registerTellerName: '',
            registerTellerId: '',
            updatedTellerId: '',
            updatedTellerName: '',
            updatedDate: '',
            updatedTime: '',
            registerDate: '',
            registerTime: ''
        } as any
        return {
            modifyCard,
            COMMON_KEY_VALUE,
            menuTree: [] as NodeData[],
            menuList: [] as MenuInfo[],
            selectedMenu: {
                menuID: "",
                menuName: "",
                menuLevel: 0,
                menuDisplayOrder: "",
                menuStatusCode: "",
                parentMenuID: "",
                menuNameKhmer: "",
                menuIconFilePath: "",
                menuIconID: '',
                state: { indeterminate: false, checked: false, selected: true, expanded: false }
            } as any,
            isAddNewMenu: false,
            isEnableEdit: false,
            isInvalidInput: false,
            menuLevelData: null as any,
            totalMenuList: 0 as number,
            menuSize: 500 as number,
            isDisabledMenuID: true,
            isDisabledMenuStatus: true,
            getImageMenuIcon: '' as any,
            previewMenuIconImageName: '' as any,
            list: [] as any,
            getImageList: [] as any,
            tempImage: '' as string,
            isHideUserCraetion: false,
            approvalRes: {} as any,
            operationMode: 'normal' as screenMode
        }
    },
    async created() {
        await this.onGetMenuInfoList();
    },
    computed: {
        menuStatusCodeValue() {
            return (statusCode: string, statusValue: string) => `${statusCode}` + ' : ' + `${statusValue}`;
        },
        renderPlaceholder(): string {
            return this.isAddNewMenu || this.isEnableEdit ? this.$t('com205001sc00.select_icon') : ''
        },
        getMenuIconIBPortal() {
            let MenuIconIDIBP = [] as any;
            if (this.$store.state.CommonCodeModule.commonCode.MenuIconIDIBP) {
                MenuIconIDIBP.push({ key: '', value: 'None' }, ...this.$store.state.CommonCodeModule.commonCode.MenuIconIDIBP);
            }
            return MenuIconIDIBP
        },
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
    },
    methods: {
        async onGetMenuInfoList() {
            const reqBody: COM20500111_REQ_BODY = {
                pageNumber: 1,
                pageSize: 1500
            }
            const responseData = await this.$request.getRequest('COM20500111', reqBody, false) as COM20500111_RES
            if (responseData.header.result) {
                const lists = responseData.body.list;
                this.totalMenuList = responseData.body.totalCount
                let rootData = lists.filter((items: any) => items.parentMenuID == '')
                let menuData = lists.filter((items: any) => items.statusCode != '09' && items.parentMenuID != '')
                this.menuList = [...rootData, ...menuData]
                this.transformAndExpandTreeData(this.menuList)
                this.approvalRequest();
            }
        },

        transformAndExpandTreeData(dataList) {
            const transformData = treeService.transformIntoTreeData(dataList, 'menuID', 'parentMenuID', 'menuName')
            this.menuTree = transformData;
            treeService.expandAllNodes(this.menuTree);
        },

        nodeSelectAtRoot() {
            return {
                menuID: "",
                menuName: "",
                menuLevel: 0,
                menuDisplayOrder: "",
                menuStatusCode: "",
                parentMenuID: "",
                state: { indeterminate: false, checked: false, selected: true, expanded: false, highLightParentNode: false }
            }
        },

        async onGetMenuInfoDetail(menuItem) {
            if (this.approvalType == '') {
                this.tempImage = ''
                if (this.isAddNewMenu || this.isEnableEdit) {
                    this.diabledSelectNewMenu();
                    this.onHighLightParentNode();
                    return
                }
                if (this.selectedMenu) {
                    this.isEnableEdit = false
                    const reqBody: COM20500112_REQ_BODY = {
                        menuID: menuItem.menuID
                    }
                    this.isHideUserCraetion = true;
                    const responseData = await this.$request.getRequest('COM20500112', reqBody, false) as COM20500112_RES
                    if (responseData.header.result) {
                        let tmpMenuLvel = Number(responseData.body.menuLevel);
                        this.menuLevelData = tmpMenuLvel += 1;
                        const menu = {
                            ...menuItem,
                            firstRegisterTellerID: responseData.body.firstRegisterTellerID,
                            firstRegisterTellerName: responseData.body.firstRegisterTellerName,
                            lastChangeTellerName: responseData.body.lastChangeTellerName,
                            lastChangeTellerID: responseData.body.lastChangeTellerID,
                            lastChangeTime: responseData.body.lastChangeTime,
                            lastChangeDate: responseData.body.lastChangeDate,
                            firstRegisterTime: responseData.body.firstRegisterTime,
                            firstRegisterDate: responseData.body.firstRegisterDate,
                            menuDisplayOrder: String(responseData.body.menuDisplayOrder),
                            menuIconID: responseData.body.menuIconID,
                        }
                        this.modifyCard = {
                            registerTellerName: responseData.body.firstRegisterTellerName,
                            registerTellerId: responseData.body.firstRegisterTellerID,
                            updatedTellerId: responseData.body.lastChangeTellerID,
                            updatedTellerName: responseData.body.lastChangeTellerName,
                            updatedDate: responseData.body.lastChangeDate,
                            updatedTime: responseData.body.lastChangeTime,
                            registerDate: responseData.body.firstRegisterDate,
                            registerTime: responseData.body.firstRegisterTime,
                        }
                        this.selectedMenu = menu;
                    }
                    this.isDisabledInputField('detail');
                }
            } else {
                this.diabledSelectNewMenu();
            }
        },

        resetStateTreeData() {
            if (this.menuList.length === 0) return
            this.menuList = this.menuList.map((menu: MenuInfo) => {
                if (menu.state) {
                    menu.state.selected = false
                }
                return menu
            })
        },

        diabledSelectNewMenu() {
            this.menuList = this.menuList.map((menu: MenuInfo) => {
                if (menu.isTempNewMenu || this.selectedMenu.menuID == menu.menuID) {
                    menu.state = { indeterminate: false, checked: false, selected: true, expanded: false }
                } else {
                    menu.state = { indeterminate: false, checked: false, selected: false, expanded: false }
                }
                return menu
            });
            this.transformAndExpandTreeData(this.menuList)
        },

        initNewMenu() {
            return {
                isTempNewMenu: true,
                menuID: "",
                parentMenuID: this.selectedMenu.menuID,
                menuName: "New Menu..",
                menuStatusCode: "01",
                menuDisplayOrder: "",
                menuIconID: "",
                nodes: [],
                state: { indeterminate: false, checked: false, selected: true, expanded: false }
            }
        },

        onClickAddMenu() {
            if (this.selectedMenu.nodes) {
                if (String(this.selectedMenu.menuLevel) != "2") {
                    this.isInvalidInput = false
                    this.isEnableEdit = true
                    this.isAddNewMenu = true
                    this.isHideUserCraetion = false;
                    this.resetStateTreeData();
                    const newMenu = this.initNewMenu()
                    this.menuList = [...this.menuList, newMenu] as MenuInfo[]
                    this.transformAndExpandTreeData(this.menuList)
                    this.selectedMenu = { ...newMenu, menuName: "" } as any
                    this.onHighLightParentNode();
                    this.isDisabledInputField('add');
                } else {
                    this.$dialog.onOpen({
                        dialogMode: 'Warning',
                        content: this.$t('com205001sc00.not_allow_menu_level_more_than_level2'),
                        callback: () => modalController.dismiss()
                    })
                }
            } else {
                this.$dialog.onOpen({
                    dialogMode: 'Warning',
                    content: this.$t('com205001sc00.please_select_specific_node_before_add'),
                    btnLabel: this.$t('common.check'),
                    callback: () => modalController.dismiss()
                })
            }
        },

        onClickRemoveMenu() {
            if (this.selectedMenu.menuID === '0' || (this.selectedMenu.nodes && this.selectedMenu.nodes.length > 0)) {
                this.$dialog.onOpen({
                    dialogMode: 'Warning',
                    content: this.$t('message.only_the_lowest_level_can_be_deleted'),
                    callback: () => modalController.dismiss()
                })
            } else {
                this.$dialog.onOpen({
                    dialogMode: 'Question',
                    content: this.$t('message.are_you_sure_you_want_to_delete_selected_menu'),
                    callback: () => this.onRemoveMenu()
                })
            }
        },

        async onRemoveMenu() {
            modalController.dismiss()
            const reqBody: COM20500141_REQ_BODY = {
                menuID: this.selectedMenu.menuID
            }
            const responseData = await this.$request.getRequest('COM20500141', reqBody, false) as COM20500141_RES
            if (responseData.header.result) {
                this.onGetMenuInfoList();
                this.menuList = this.menuList.filter((menu: MenuInfo) => menu.menuID !== this.selectedMenu?.menuID);
                this.transformAndExpandTreeData(this.menuList);
                this.clearForm();
                this.$dialog.onOpen({
                    dialogMode: 'Success',
                    content: this.$t('com205001sc00.remove_menu_success'),
                    callback: () => modalController.dismiss()
                })
            }
        },

        clearForm() {
            this.selectedMenu.menuName = '';
            this.selectedMenu.menuID = '';
            this.selectedMenu.menuIconID = '';
            this.selectedMenu.menuDisplayOrder = '';
            this.selectedMenu.menuStatusCode = '';
            this.isHideUserCraetion = false;
        },

        onCheckValidate() {
            if (this.selectedMenu.menuID === '' || this.selectedMenu.menuName === '') {
                this.isInvalidInput = true
                return true
            }
            this.isInvalidInput = false
            return false
        },

        onClickSave() {
            if (this.onCheckValidate()) {
                return;
            } else {
                this.$dialog.onOpen({
                    dialogMode: 'Question',
                    content: this.$t('message.would_you_like_to_save'),
                    callback: () => {
                        modalController.dismiss()
                        if (this.isAddNewMenu) {
                            this.onRegisterMenuInfo()
                        } else if (!this.isAddNewMenu) {
                            this.onUpdateMenuInfo();
                        }
                    }
                })
            }
        },

        onClickEdit() {
            this.isEnableEdit = true
            this.isAddNewMenu = false
            this.isDisabledInputField('edit');
        },

        async onRegisterMenuInfo() {
            if (this.selectedMenu.menuName != '') {
                this.list.push({
                    menuName: this.selectedMenu.menuName,
                    languageCode: 'En'
                })
            }
            if (this.selectedMenu && this.selectedMenu.parentMenuID) {
                const reqBody: COM20500121_REQ_BODY = {
                    menuID: this.selectedMenu.menuID,
                    menuName: this.selectedMenu.menuName,
                    parentMenuID: this.selectedMenu.parentMenuID,
                    menuDisplayOrder: Number(this.selectedMenu.menuDisplayOrder),
                    menuLevel: String(this.menuLevelData) as any,
                    menuStatusCode: this.selectedMenu.menuStatusCode as any,
                    menuIconID: this.selectedMenu.menuIconID,
                    list: this.list as any
                }

                const responseData = await this.$request.getRequest('COM20500121', reqBody, false) as COM20500121_RES
                if (responseData.header.result) {
                    this.onUpdateLocalData();
                    this.list = [];
                    this.clearForm();
                    this.$dialog.onOpen({
                        dialogMode: 'Success',
                        content: this.$t('com205001sc00.register_menu'),
                        callback: () => modalController.dismiss()
                    })
                    this.isEnableEdit = false;
                    this.callbackCancel();
                }
            }
            this.isDisabledInputField('register');
        },

        async onUpdateMenuInfo() {
            if (this.selectedMenu.menuName != '') {
                this.list.push({
                    menuName: this.selectedMenu.menuName,
                    languageCode: 'En'
                })
            }
            if (this.selectedMenu && this.selectedMenu.parentMenuID) {
                const reqBody: COM20500131_REQ_BODY = {
                    menuID: this.selectedMenu.menuID,
                    menuName: this.selectedMenu.menuName,
                    parentMenuID: this.selectedMenu.parentMenuID,
                    menuDisplayOrder: Number(this.selectedMenu.menuDisplayOrder),
                    menuLevel: String(this.selectedMenu.menuLevel) as any,
                    menuStatusCode: this.selectedMenu.menuStatusCode as any,
                    menuIconID: this.selectedMenu.menuIconID,
                    list: this.list as any
                }
                const responseData = await this.$request.getRequest('COM20500131', reqBody, false) as COM20500131_RES

                if (responseData.header.result) {
                    this.onUpdateLocalData();
                    this.list = [];
                    this.clearForm()
                    this.$dialog.onOpen({
                        dialogMode: 'Success',
                        content: this.$t('com205001sc00.update_menu'),
                        callback: () => modalController.dismiss()
                    })
                    this.isEnableEdit = false;
                    this.callbackCancel();
                }
            }
            this.isDisabledInputField('save')
        },

        onUpdateLocalData() {
            this.transformAndExpandTreeData(this.menuList)
            this.isAddNewMenu = false
            this.isEnableEdit = false;
            this.onGetMenuInfoList();
            this.onResetHighLightParentNode();
        },

        onClickCancel() {
            if (!this.selectedMenu && this.isEnableEdit) return
            if (this.isAddNewMenu) {
                this.onCancelAddNewMenu();
            } else {
                this.onCancelUpdateMenu()
            }
        },

        onCancelUpdateMenu() {
            this.$dialog.onOpen({
                dialogMode: 'Question',
                content: this.$t('message.are_you_sure_you_want_to_cancel_without_saving_data'),
                callback: () => {
                    this.callbackCancel();
                }
            })
        },

        onCancelAddNewMenu() {
            this.$dialog.onOpen({
                dialogMode: 'Question',
                content: this.$t('message.are_you_sure_you_want_to_cancel_without_saving_data'),
                callback: () => this.callbackCancel()
            })
        },

        callbackCancel() {
            this.isDisabledMenuID = true;
            this.isDisabledMenuStatus = true;
            this.isEnableEdit = false;
            this.isAddNewMenu = false
            this.isInvalidInput = false;
            this.menuList = this.menuList.filter((menu: MenuInfo) => !menu.isTempNewMenu);
            this.transformAndExpandTreeData(this.menuList)
            this.selectedMenu = this.nodeSelectAtRoot()
            this.onResetHighLightParentNode();
            modalController.dismiss();
        },

        onHighLightParentNode() {
            if (this.selectedMenu.isTempNewMenu) {
                let result = this.flattenTree(this.menuTree);
                result.find((list) => {
                    if (list.menuID == this.selectedMenu.parentMenuID) {
                        list.state.highLightParentNode = true;
                    }
                });
            }
        },

        onResetHighLightParentNode() {
            let result = this.flattenTree(this.menuTree);
            result.find((list) => {
                if (list.state.highLightParentNode) {
                    list.state.highLightParentNode = false;
                }
            });
            return result
        },

        flattenTree(tree: any) {
            const flattenedTree = [] as any
            tree.forEach((node: any) => {
                flattenedTree.push(node);
                if (node.nodes.length > 0) {
                    const childNodes = this.flattenTree(node.nodes);
                    flattenedTree.push(...childNodes);
                }
            });
            return flattenedTree;
        },

        isDisabledInputField(value: string) {
            switch (value) {
                case 'detail':
                    this.isDisabledMenuID = true;
                    this.isDisabledMenuStatus = true;
                    break;
                case 'edit':
                    if ((this.selectedMenu.nodes && this.selectedMenu.nodes.length > 0)) {
                        this.isDisabledMenuID = true;
                        this.isDisabledMenuStatus = true;
                    } else {
                        this.isDisabledMenuID = true;
                        this.isDisabledMenuStatus = false
                    }
                    break;
                case 'add':
                    this.isDisabledMenuID = false;
                    this.isDisabledMenuStatus = true;
                    break;
                default:
                    this.isDisabledMenuID = true;
                    this.isDisabledMenuStatus = true;
                    break;
            }
        },
        async onClickUploadMenuIcon(event: any) {
            this.onclickRemoveMenuIcon();
            const image = event.target.files[0];
            if (image.type != "image/svg+xml") {
                return '';
            } else {
                const render = new FileReader();
                render.readAsDataURL(image);
                render.onload = event => {
                    this.selectedMenu.menuIconFilePath = event.target?.result;
                    this.previewMenuIconImageName = image.name;
                }
                this.getImageMenuIcon = image;
            }
        },

        onclickRemoveMenuIcon() {
            this.selectedMenu.menuIconFilePath = '';
            this.previewMenuIconImageName = ''
        },

        async approvalRequest() {
            if (this.approvalType != '') {
                await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                    let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                    this.approvalRes = authDetail.body
                    this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
                    if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                        const reqBody: COM20500112_REQ_BODY = {
                            menuID: authDetailParsed.menuID
                        }
                        const responseData = await this.$request.getRequest('COM20500112', reqBody, false) as COM20500112_RES;
                        this.selectedMenu = {
                            menuName: responseData.body.menuName,
                            menuID: responseData.body.menuID,
                            parentMenuID: responseData.body.parentMenuID,
                            menuLevel: responseData.body.menuLevel,
                            menuStatusCode: responseData.body.menuStatusCode,
                            menuDisplayOrder: responseData.body.menuDisplayOrder,
                            menuIconID: responseData.body.menuIconID,
                            state: { indeterminate: true, checked: false, selected: true, expanded: false }
                        }
                        let treeData = this.flattenTree(this.menuTree);
                        treeData.find((list) => {
                            if (list.menuID == this.selectedMenu.menuID) {
                                list.state.selected = true;
                            }
                        });
                        setTimeout(() => {
                            let treeParentClass: any = document.querySelector('#tree_active')
                            if (treeParentClass) {
                                const activeChildrenClass = treeParentClass.querySelector('.active');
                                if (activeChildrenClass) {
                                    activeChildrenClass.scrollIntoView({ behavior: "smooth", block: "center" });
                                }
                            }
                        }, 50);
                        this.getCreationDetailWithApprovalMode();
                    } else if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'update') {
                        this.selectedMenu = {
                            menuName: authDetailParsed.menuName,
                            menuID: authDetailParsed.menuID,
                            parentMenuID: authDetailParsed.parentMenuID,
                            menuLevel: authDetailParsed.menuLevel,
                            menuStatusCode: authDetailParsed.menuStatusCode,
                            menuDisplayOrder: authDetailParsed.menuDisplayOrder,
                            menuIconID: authDetailParsed.menuIconID,
                            state: { indeterminate: true, checked: false, selected: true, expanded: false }
                        }
                        let treeData = this.flattenTree(this.menuTree);
                        treeData.find((list) => {
                            if (list.menuID == this.selectedMenu.menuID) {
                                list.state.selected = true;
                            }
                        });
                        setTimeout(() => {
                            let treeParentClass: any = document.querySelector('#tree_active');
                            if (treeParentClass) {
                                const activeChildrenClass = treeParentClass.querySelector('.active');
                                if (activeChildrenClass) {
                                    activeChildrenClass.scrollIntoView({ behavior: "smooth", block: "center" });
                                }
                            }
                        }, 50);
                        this.getCreationDetailWithApprovalMode();
                    } else {
                        this.selectedMenu = {
                            menuName: authDetailParsed.menuName,
                            menuID: authDetailParsed.menuID,
                            parentMenuID: authDetailParsed.parentMenuID,
                            menuLevel: authDetailParsed.menuLevel,
                            menuStatusCode: authDetailParsed.menuStatusCode,
                            menuDisplayOrder: authDetailParsed.menuDisplayOrder,
                            menuIconID: authDetailParsed.menuIconID,
                            state: { indeterminate: true, checked: false, selected: true, expanded: false }
                        }
                        this.menuList = [...this.menuList, this.selectedMenu] as MenuInfo[];
                        this.transformAndExpandTreeData(this.menuList)
                        setTimeout(() => {
                            let treeParentClass: any = document.querySelector('#tree_active')
                            if (treeParentClass) {
                                const activeChildrenClass = treeParentClass.querySelector('.active');
                                if (activeChildrenClass) {
                                    activeChildrenClass.scrollIntoView({ behavior: "smooth", block: "center" });
                                }
                            }
                        }, 50);
                    }
                });
            }
        },

        async getCreationDetailWithApprovalMode() {
            const reqBody: COM20500112_REQ_BODY = {
                menuID: this.selectedMenu.menuID
            }
            const responseData = await this.$request.getRequest('COM20500112', reqBody, false) as COM20500112_RES
            if (responseData.header.result) {
                this.modifyCard = {
                    registerTellerName: responseData.body.firstRegisterTellerName,
                    registerTellerId: responseData.body.firstRegisterTellerID,
                    updatedTellerId: responseData.body.lastChangeTellerID,
                    updatedTellerName: responseData.body.lastChangeTellerName,
                    updatedDate: responseData.body.lastChangeDate,
                    updatedTime: responseData.body.lastChangeTime,
                    registerDate: responseData.body.firstRegisterDate,
                    registerTime: responseData.body.firstRegisterTime,
                }
            }
        }

    }
})
